<template>
	<div>
		<el-breadcrumb separator="/">
			<el-breadcrumb-item>系统管理</el-breadcrumb-item>
			<el-breadcrumb-item>审核</el-breadcrumb-item>
		</el-breadcrumb>

		<el-card>
			<div >
				<el-button @click="exaArray()" type="primary">批量审核</el-button>
				<el-button @click="toggleSelection()" type="danger">取消选择</el-button>
			</div>
			 <el-divider></el-divider>
			<el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" max-height="1000px" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column type="selection" width="55"> </el-table-column>
				<el-table-column prop="name" label="姓名" width="300"> </el-table-column>
				<el-table-column prop="number" label="学号" width="400"> </el-table-column>
				<el-table-column prop="sex" label="性别" width="150"> </el-table-column>
				<el-table-column prop="acdemic" label="学院" width="400"> </el-table-column>
				<el-table-column prop="status" label="状态" width="150">
					<template slot-scope="scope">
						<el-switch v-model="scope.row.status" @change="userstatechange(scope.row)">
						</el-switch>
					</template>
				</el-table-column>
			</el-table>
		</el-card>



	</div>
</template>

<script>
	import eventbus from '../../public/eventbus.js'
	export default {
		name: 'examnine',
		data() {
			return {
				multipleSelection: [],
				exaarr: [],
				tableData: [{
						name: '张三',
						number: '123456',
						sex: '男',
						acdemic: '控计',
						status: false,
					},
					{
						name: '张三',
						number: '12',
						sex: '男',
						acdemic: '控计',
						status: false,
					},
					{
						name: '张三',
						number: '12345',
						sex: '男',
						acdemic: '控计',
						status: false,
					}

				]
			}
		},
		// created() {
		// 	this.gettabledata()
		// },
		methods: {
			toggleSelection() {
				this.$refs.multipleTable.clearSelection();
			},
			userstatechange(userinfo) {
				console.log(userinfo);
				return this.$message({
					message: '更新成功',
					type: 'success'
				});
			},
			gettabledata() {

			},
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
			async exaArray(){
				this.exaarr=[];
				for(var i=0;i<this.multipleSelection.length;i++)
				{
					this.exaarr[i]=this.multipleSelection[i].number
				}
				const {data:res}=await this.$http.post("department/service/approve",this.exaarr,{headers: {'Content-Type': 'application/x-www-form-urlencoded'}});
				
			}
		}
	}
</script>

<style scoped>
	.el-breadcrumb {
		margin-bottom: 15px;
	}
</style>
